.popup__container_with_form {
  width: 100%;
  max-width: 430px;
  padding: 36px;
  box-sizing: border-box;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0px 0px 25px;
}

@media screen and (max-width: 600px) {
  .popup__container_with_form {
    padding: 30px 20px;
  }
}
